body {
  margin: 0;
  padding: 0;
}

.scroll * {
  position: relative; /* z-index는 position이 지정된 요소에만 적용됨 */
  overflow: auto; /* https://stackoverflow.com/questions/9519841/why-does-this-css-margin-top-style-not-work 아직 이해는 못함.. */
  /* margin: 10px; 아니 왜 여기다 마진을 주면 자식 요소들에게 마진이 생기는 거지!!!! */
}

.ellipse {
  display: inline-block;
  width: 50px;
  height: 100px;
  border-radius: 25px / 50px; /* 엥 / 썼을 때랑 안 썼을 때랑 다르다..! */
  border: 2px solid;
  z-index: 3;
}

.ellipse.right {
  margin-left: -25px;
  z-index: 1;
}

.side {
  display: inline-block;
  /* margin-top: -100px; */
  margin-left: -25px;
  width: 150px;
  height: 100px;
  border: 2px solid;
  border-right-width: 0px;
  z-index: 2;
}

.flex-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  padding-left: 25px;
}

.flex-container * {
  margin: 0px;
}

.scrolls-container {
  width: 60%;
  min-width: 920px;
  margin: auto;
  display: grid;
  grid-template-columns: 25% 25% 25% 25%;
}
.from {
  text-align: right;
}
.paper {
  border: 2px solid;
  width: 60%;
  margin: auto;
  display: grid;
  grid-template-columns: 25% 25% 25% 25%;
  min-height: 200px;
  grid-template-rows: 50px auto;
}

.title {
  grid-column-start: 1;
  grid-column-end: 5;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.write-bt {
  border: none;
  cursor: pointer;
}

.write-bt:focus {
  outline: none;
}


header {
  border-bottom: 2px solid mediumslateblue;
  background-color: ghostwhite;
  height: 40px; /* 왜 %가 안 먹히지 */
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}
.App {
  text-align: center;
}

